@font-face {
  font-family: DM Sans;
  src: url("DMSans-Regular.c108cb9e.woff"), url("DMSans-Regular.461ab9f9.woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: DM Sans;
  src: url("DMSans-Italic.73116179.woff"), url("DMSans-Italic.108082e4.woff2");
  font-weight: 400;
  font-display: swap;
  font-style: italic;
}

@font-face {
  font-family: DM Sans;
  src: url("DMSans-Medium.7d344570.woff"), url("DMSans-Medium.63db2800.woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: DM Sans;
  src: url("DMSans-Bold.8954ee88.woff2"), url("DMSans-Bold.a9858b2f.woff");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

p, h1, h2, h3 {
  margin: 0;
}

ul {
  margin: 0;
  padding-left: 0;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

body {
  font-family: DM Sans, sans-serif;
}

.list {
  list-style: none;
}

.link {
  text-decoration: none;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

:root {
  --light-theme-bcg: #f6f6f6;
  --light-theme-bcg-header: #fff;
  --ligth-theme-bcg-shoplist-block: #fff;
  --light-theme-primary-text-color: #111;
  --light-theme-second-text-color: #b4afaf;
  --light-theme-categories-text-color: rgba(17, 17, 17, .6);
  --dark-theme-bcg: #202024;
  --dark-theme-bcg-header: #111;
  --dark-theme-bcg-shoplist-block: #111;
  --dark-theme-primary-text-color: #fff;
  --dark-theme-second-text-color: #b4afaf;
  --dark-theme-categories-text-color: rgba(255, 255, 255, .6);
  --gradient-bcg-color: radial-gradient( 183.6% 183.01% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100% );
  --dump-bcg-color: #4f2ee8;
  --home-bcg-color: #eac645;
  --transition-func: cubic-bezier(.4, 0, .2, 1);
}

.container {
  width: 100%;
  max-width: 375px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    max-width: 1440px;
    padding-left: 24px;
    padding-right: 24px;
  }
}

.header {
  z-index: 100;
  width: 100%;
  position: fixed;
}

.header-wrap {
  border: 1.5px solid var(--light-theme-primary-text-color);
  background-color: var(--light-theme-bcg-header);
  border-radius: 0 0 8px 8px;
  align-items: center;
  padding-top: 18px;
  padding-bottom: 18px;
  display: flex;
}

.header-logo {
  align-items: center;
  display: flex;
}

.header-logo-icon {
  margin-right: 8px;
}

.header-nav {
  display: none;
}

.toggle-wrap {
  margin-left: auto;
  margin-right: 14px;
}

.toggle-button {
  width: 40px;
  height: 20px;
  vertical-align: top;
  cursor: pointer;
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  transition: all 250ms var(--transition-func);
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  border-radius: 30px;
  outline: none;
  margin: 0;
  display: inline-block;
  position: relative;
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.toggle-button:after {
  content: "";
  width: 17.5px;
  height: 17.5px;
  transition: all 250ms var(--transition-func);
  background: linear-gradient(#dcdcdc 0%, #f7f7f7 100%);
  border-radius: 50%;
  display: inline-block;
  position: absolute;
  top: 1px;
  left: 1px;
  transform: translateX(0);
}

.toggle-button:checked:after {
  background: linear-gradient(#dcdcdc 0%, #f7f7f7 100%);
  transform: translateX(calc(100% + 3px));
}

.toggle-button:checked {
  background: linear-gradient(#4f2ee8 0%, #686868 100%);
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.header-user {
  display: none;
}

.mob-open-btn, .mob-close-btn {
  stroke: var(--light-theme-primary-text-color);
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 0;
  display: flex;
}

.mob-close-btn {
  display: none;
}

.mob-container {
  width: 100%;
  height: 100%;
  z-index: 99;
  transition: transform 250ms var(--transition-func);
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
  transform: translateY(-100%);
}

.mob-container.is-open {
  transform: translateY(0);
}

.mob-menu-wrap {
  height: 100%;
  background: url("menu-group.a23b4f52.png") 50% 100% no-repeat, var(--gradient-bcg-color);
  flex-direction: column;
  padding-top: 84px;
  padding-bottom: 20px;
  display: flex;
}

.mob-user {
  align-items: center;
  margin-bottom: 130px;
  display: flex;
}

.header-user-img, .mob-user-img {
  border-radius: 37px;
  margin-right: 9px;
}

.header-user-name, .mob-user-name {
  color: var(--light-theme-bcg);
  letter-spacing: -.44px;
  margin-right: 4px;
  font-size: 22px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

.mob-nav-wrap {
  justify-content: center;
  display: flex;
}

.mob-header-list-home {
  margin-bottom: 16px;
  display: inline-block;
}

.header-link-home, .mob-link-home {
  color: var(--light-theme-primary-text-color);
  letter-spacing: -.56px;
  text-transform: uppercase;
  font-size: 14px;
  font-style: normal;
  line-height: 1.29;
}

.header-link-shopping, .mob-link-shopping {
  color: var(--ligth-theme-bcg-shoplist-block);
  text-transform: uppercase;
  letter-spacing: -.56px;
  align-items: center;
  font-size: 14px;
  font-style: normal;
  line-height: 1.29;
  display: flex;
}

.shopping-icon {
  fill: var(--ligth-theme-bcg-shoplist-block);
  margin-left: 4px;
}

.active-tab {
  background-color: var(--home-bcg-color);
  cursor: pointer;
  transition: all 250ms var(--transition-func);
  border: 2px solid rgba(0, 0, 0, 0);
  border-radius: 36px;
  padding: 8px 16px;
  font-weight: 700;
}

.active-link-mob {
  color: var(--light-theme-primary-text-color);
}

.active-shopping-icon {
  fill: var(--light-theme-primary-text-color);
}

.no-active-tab {
  transition: all 250ms var(--transition-func);
  font-weight: 500;
}

.mob-user-logout {
  border: 1.5px solid var(--light-theme-primary-text-color);
  width: 118px;
  height: 48px;
  border-radius: 18px;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  bottom: 20px;
}

.mob-user-logout-text {
  color: var(--light-theme-primary-text-color);
  letter-spacing: -.56px;
  margin-right: 4px;
  font-family: DM Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.29;
}

.btn-hide {
  display: none;
}

.btn-show {
  display: flex;
}

@media screen and (min-width: 768px) {
  .mob-open-btn {
    display: none;
  }

  .header-wrap {
    padding-top: 14px;
    padding-bottom: 13px;
  }

  .header-logo {
    margin-right: 40px;
  }

  .header-nav {
    display: block;
  }

  .header-list {
    align-items: center;
    display: flex;
  }

  .header-link-shopping {
    color: var(--light-theme-primary-text-color);
  }

  .shopping-icon {
    fill: var(--light-theme-primary-text-color);
  }

  .active-tab {
    margin-right: 16px;
  }

  .no-active-tab, .toggle-wrap {
    margin-right: 24px;
  }

  .header-user {
    width: 184px;
    height: 45px;
    background: var(--dump-bcg-color);
    border-color: rgba(0, 0, 0, 0);
    border-radius: 36px;
    align-items: center;
    display: flex;
  }

  .header-user svg {
    fill: var(--light-theme-bcg-header);
  }
}

@media screen and (min-width: 1440px) {
  .header-logo {
    transition: all 250ms var(--transition-func);
    margin-right: 76px;
  }

  .header-logo:-webkit-any(:hover, :focus) {
    transform: translateX(20%);
  }

  .header-logo:is(:hover, :focus) {
    transform: translateX(20%);
  }

  .active-tab {
    margin-right: 24px;
  }

  .header-list-shopping span {
    transform-style: preserve-3d;
    transition: all 250ms var(--transition-func);
  }

  .header-list-shopping span:after {
    width: 100%;
    content: "Buy";
    transform-origin: 0 100%;
    position: absolute;
    top: -100%;
    left: 0;
    transform: rotateX(90deg);
  }

  .header-list-shopping span:not(:lang(ae)):not(:lang(ar)):not(:lang(arc)):not(:lang(bcc)):not(:lang(bqi)):not(:lang(ckb)):not(:lang(dv)):not(:lang(fa)):not(:lang(glk)):not(:lang(he)):not(:lang(ku)):not(:lang(mzn)):not(:lang(nqo)):not(:lang(pnb)):not(:lang(ps)):not(:lang(sd)):not(:lang(ug)):not(:lang(ur)):not(:lang(yi)):after {
    text-align: right;
  }

  .header-list-shopping span:-webkit-any(:lang(ae), :lang(ar), :lang(arc), :lang(bcc), :lang(bqi), :lang(ckb), :lang(dv), :lang(fa), :lang(glk), :lang(he), :lang(ku), :lang(mzn), :lang(nqo), :lang(pnb), :lang(ps), :lang(sd), :lang(ug), :lang(ur), :lang(yi)):after {
    text-align: left;
  }

  .header-list-shopping span:is(:lang(ae), :lang(ar), :lang(arc), :lang(bcc), :lang(bqi), :lang(ckb), :lang(dv), :lang(fa), :lang(glk), :lang(he), :lang(ku), :lang(mzn), :lang(nqo), :lang(pnb), :lang(ps), :lang(sd), :lang(ug), :lang(ur), :lang(yi)):after {
    text-align: left;
  }

  .header-list-shopping:is(:hover, :focus) span {
    transform-origin: bottom;
    transform: rotateX(-90deg)translateY(100%);
  }

  .header-user, .header-user-name, .header-user-name svg {
    transition: all 250ms var(--transition-func);
  }

  .active-tab:-webkit-any(:hover, :focus), .header-user:-webkit-any(:hover, :focus) {
    border: 2px solid var(--dump-bcg-color);
    background-color: var(--light-theme-bcg-header);
  }

  .active-tab:is(:hover, :focus), .header-user:is(:hover, :focus) {
    border: 2px solid var(--dump-bcg-color);
    background-color: var(--light-theme-bcg-header);
  }

  .header-user:is(:hover, :focus) .header-user-name {
    color: var(--light-theme-primary-text-color);
  }

  .header-user:is(:hover, :focus) svg {
    fill: var(--light-theme-primary-text-color);
  }

  .no-active-tab:-webkit-any(:hover, :focus) {
    transform: translateX(20%);
  }

  .no-active-tab:is(:hover, :focus) {
    transform: translateX(20%);
  }
}

body {
  background-color: var(--light-theme-bcg);
}

.categories {
  margin-bottom: 40px;
  display: inline-block;
}

a {
  text-decoration: none;
}

.categories_list {
  width: 335px;
  height: 228px;
  cursor: pointer;
  list-style: none;
  overflow-x: hidden;
  overflow-y: scroll;
}

.categories_list::-webkit-scrollbar {
  width: 8px;
  height: 114px;
}

.categories_list::-webkit-scrollbar-thumb {
  background-color: #e0e0e0;
  border-radius: 12px;
}

.categories_list::-webkit-scrollbar-thumb:hover {
  background-color: var(--light-theme-second-text-color);
}

.categories-list {
  scrollbar-width: 10px;
}

.category_list_element:not(:last-child) {
  margin-bottom: 24px;
}

.category_list_element {
  color: var(--light-theme-categories-text-color);
  letter-spacing: -.32px;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.125;
}

.category_list_element.chosen_category {
  color: var(--dump-bcg-color);
  text-transform: uppercase;
  font-weight: 700;
}

li {
  list-style: none;
}

@media screen and (min-width: 768px) {
  .categories_list {
    width: 337px;
    height: 472px;
  }

  .categories {
    margin-bottom: 0;
  }

  .category_list_element:not(:last-child) {
    margin-bottom: 32px;
  }

  .categories_list::-webkit-scrollbar {
    width: 8px;
    height: 167px;
  }

  .category_list_element {
    letter-spacing: -.36px;
    font-size: 18px;
    line-height: 1.33;
  }
}

@media screen and (min-width: 1440px) {
  .categories_list {
    width: 356px;
  }

  .category_list_element:not(:last-child) {
    margin-bottom: 32px;
  }

  .category_list_element {
    letter-spacing: -.36px;
    transition: all 250ms var(--transition-func);
    font-size: 18px;
    line-height: 1.33;
    transform: scale(1)translateX(0);
  }

  .category_list_element:-webkit-any(:hover, :focus) {
    transform: scale(1.06)translateX(6%);
  }

  .category_list_element:is(:hover, :focus) {
    transform: scale(1.06)translateX(6%);
  }
}

.support-container {
  width: 335px;
  height: 352px;
  background: radial-gradient(189.32% 190.93% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
  border-radius: 16px;
  margin-bottom: 40px;
  padding-top: 28px;
