  font-size: 22px;
  font-style: normal;
  font-weight: 700;
  line-height: 109%;
  display: flex;
}

.svg-title {
  margin-left: 12px;
}

.support-list {
  height: 188px;
  flex-direction: column;
  gap: 20px;
  margin-top: 44px;
  margin-bottom: 16px;
  padding-left: 40px;
  list-style: none;
  display: flex;
  overflow: auto;
}

.support-list::-webkit-scrollbar {
  width: 0;
}

.support-item {
  height: 32px;
  display: flex;
}

.support-span {
  color: #fff;
  letter-spacing: -.56px;
  margin-right: 16px;
