}

.support-btn-top {
  width: 32px;
  height: 32px;
  cursor: pointer;
  background-color: #4f2ee8;
  border: 1px solid #4f2ee8;
  border-radius: 50%;
  align-items: center;
  margin: auto;
  padding: 6px;
  display: none;
  transform: rotate(180deg);
  box-shadow: 0 0 0 10px rgba(79, 46, 232, .3);
}

.btn-svg-top, .support-btn:focus .btn-svg, .support-btn:hover .btn-svg {
  fill: #fff;
}

@media screen and (min-width: 768px) {
  .support-container {
